.loading {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.load1c {
  width: 100%;
  height: 10px;
  border-radius: 10px;
  overflow: hidden;
}

.load1 {
  background: linear-gradient(
    to right,
    rgb(230, 230, 230),
    rgb(185, 185, 186),
    rgb(230, 230, 230)
  );
  height: 100%;
  width: 0%;
  animation: load 1s linear infinite;
}

.load2c {
  width: 70% !important;
  height: 10px;
  border-radius: 10px;
  overflow: hidden;
}

.load2 {
  background: linear-gradient(
    to right,
    rgb(230, 230, 230),
    rgb(185, 185, 186),
    rgb(230, 230, 230)
  );
  height: 100%;
  width: 0%;
  animation: load 1s linear infinite;
}

.load3c {
  width: 50% !important;
  height: 10px;
  border-radius: 10px;
  overflow: hidden;
}

.load3 {
  background: linear-gradient(
    to right,
    rgb(230, 230, 230),
    rgb(185, 185, 186),
    rgb(230, 230, 230)
  );
  height: 100%;
  width: 0%;
  animation: load 1s linear infinite;
}
@keyframes load {
  0% {
    width: 0%;
    transform: scaleY(1); /* Hiệu ứng phồng */
  }
  50% {
    transform: scaleY(1.1); /* Tăng kích thước tại giữa */
  }
  100% {
    width: 100%;
    transform: scaleY(1); /* Quay lại kích thước ban đầu */
  }
}